import React, { useEffect, useState } from "react";
import "./ArtifactDisplay.scss";
import { Prism as SyntaxHighlighter } from "react-syntax-highlighter";
import { oneDark } from "react-syntax-highlighter/dist/esm/styles/prism";

export default function ArtifactDisplay({
  title,
  codePath,
  iframeSrc,
  language = "html",
  reflection
}) {
  const [code, setCode] = useState("");

  useEffect(() => {
    if (codePath) {
      fetch(codePath)
        .then((res) => res.text())
        .then((data) => setCode(data))
        .catch((err) => {
          console.error("Error loading artifact code:", err);
          setCode("// Could not load the code.");
        });
    }
  }, [codePath]);

  return (
    <div className="artifact-container">
      <h4 className="artifact-title">{title}</h4>
      <p className="artifact-lang">Language: {language.toUpperCase()}</p>

      <div className="artifact-content">
        <div className="artifact-code">
          <SyntaxHighlighter language={language} style={oneDark} wrapLines={true}>
            {code}
          </SyntaxHighlighter>
        </div>

        {iframeSrc ? (
          <iframe
            src={iframeSrc}
            title={title}
            className="artifact-preview"
            frameBorder="0"
            scrolling="no"
            sandbox="allow-scripts allow-same-origin"
          />
        ) : (
          <div className="artifact-placeholder">Live preview not available</div>
        )}
            </div>
            {reflection && (
              <div className="artifact-reflection">
                <h5>Reflection</h5>
                <p>{reflection}</p>
              </div>
            )}
            </div>

  );
}
